// src/components/TextCompare.js
import React, { useState, useEffect, useRef } from 'react';
import MonacoEditor from '@monaco-editor/react';
import { diffWords } from 'diff';
import './TextCompare.css';

const STORAGE_KEY_1 = 'text-compare-left';
const STORAGE_KEY_2 = 'text-compare-right';

const monacoOptions = {
  selectOnLineNumbers: true,
  roundedSelection: false,
  readOnly: false,
  cursorStyle: 'line',
  automaticLayout: true,
  wordWrap: 'off',
  scrollBeyondLastLine: false,
  minimap: { enabled: false },
  fontSize: 16,
  lineNumbers: 'on',
  scrollbar: {
    vertical: 'auto',
    horizontal: 'auto',
  },
};

function getDecorations(text, otherText, side, monaco) {
  if (!monaco) return [];
  const diffs = diffWords(text, otherText);
  let decorations = [];
  let pos = 0;
  for (let i = 0; i < diffs.length; i++) {
    const part = diffs[i];
    const length = part.value.length;
    if (side === 'left') {
      if (part.removed) {
        // Highlight removed text in red
        const start = pos + 1;
        const end = pos + length + 1;
        const startPos = monaco.Position.fromString(`${start}`);
        const endPos = monaco.Position.fromString(`${end}`);
        decorations.push({
          range: {
            startLineNumber: text.slice(0, start - 1).split('\n').length,
            startColumn: start - text.lastIndexOf('\n', start - 2),
            endLineNumber: text.slice(0, end - 1).split('\n').length,
            endColumn: end - text.lastIndexOf('\n', end - 2),
          },
          options: {
            inlineClassName: 'monaco-diff-removed',
          },
        });
      }
      if (!part.added) pos += length;
    } else {
      if (part.added) {
        // Highlight added text in green
        const start = pos + 1;
        const end = pos + length + 1;
        decorations.push({
          range: {
            startLineNumber: otherText.slice(0, start - 1).split('\n').length,
            startColumn: start - otherText.lastIndexOf('\n', start - 2),
            endLineNumber: otherText.slice(0, end - 1).split('\n').length,
            endColumn: end - otherText.lastIndexOf('\n', end - 2),
          },
          options: {
            inlineClassName: 'monaco-diff-added',
          },
        });
      }
      if (!part.removed) pos += length;
    }
  }
  return decorations;
}

const TextCompare = () => {
  const [text1, setText1] = useState('');
  const [text2, setText2] = useState('');
  const leftEditorRef = useRef(null);
  const rightEditorRef = useRef(null);
  const monacoRef = useRef(null);
  const leftDecorations = useRef([]);
  const rightDecorations = useRef([]);

  useEffect(() => {
    setText1(localStorage.getItem(STORAGE_KEY_1) || '');
    setText2(localStorage.getItem(STORAGE_KEY_2) || '');
  }, []);

  useEffect(() => {
    localStorage.setItem(STORAGE_KEY_1, text1);
  }, [text1]);
  useEffect(() => {
    localStorage.setItem(STORAGE_KEY_2, text2);
  }, [text2]);

  // Decorations for left editor
  useEffect(() => {
    if (leftEditorRef.current && monacoRef.current) {
      const decorations = getDecorations(text1, text2, 'left', monacoRef.current);
      leftDecorations.current = leftEditorRef.current.deltaDecorations(leftDecorations.current, decorations);
    }
  }, [text1, text2]);

  // Decorations for right editor
  useEffect(() => {
    if (rightEditorRef.current && monacoRef.current) {
      const decorations = getDecorations(text2, text1, 'right', monacoRef.current);
      rightDecorations.current = rightEditorRef.current.deltaDecorations(rightDecorations.current, decorations);
    }
  }, [text1, text2]);

  return (
    <div className="text-compare-wrapper">
      <div className="monaco-editor-container">
        <MonacoEditor
          height="100vh"
          width="100%"
          language="json"
          theme="vs-dark"
          value={text1}
          onChange={v => setText1(v || '')}
          options={monacoOptions}
          onMount={(editor, monaco) => {
            leftEditorRef.current = editor;
            monacoRef.current = monaco;
          }}
        />
      </div>
      <div className="monaco-editor-container">
        <MonacoEditor
          height="100vh"
          width="100%"
          language="json"
          theme="vs-dark"
          value={text2}
          onChange={v => setText2(v || '')}
          options={monacoOptions}
          onMount={(editor, monaco) => {
            rightEditorRef.current = editor;
            monacoRef.current = monaco;
          }}
        />
      </div>
    </div>
  );
};

export default TextCompare;
